header{
    padding: 1rem;
    width: 100%;
}
header nav{
    display: flex;
    justify-content: space-between;
    gap: 1.5rem;
}
header nav ul{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 1.5rem;

    li{
        font-size: 24px;
        line-height: 28px;
        letter-spacing: 0.3px;

        a{
            color:#252733;
            text-decoration: none;
        }
    }
    img{
        border-radius: 15px;
    }
}

