.tickets{
    padding: 1rem;
    text-align: center;
    border: 1px solid gray;
    border-radius: 10px;


    input{
        width: 50%;
        height: 50px;
        overflow: hidden;
    }
}


.students{
    

    img{
        border-radius: 10px;
    }

    button{
        width: 50px;
        height: 15px;
        font-size: 11px;
        line-height: 13px;
        text-align: center;
        letter-spacing: 0.5px;
        color: white;
        text-transform: uppercase;
        background-color: #F12B2C;
        border: none;
        border-radius: 15px;
        cursor: pointer;

    }
}
