.card__wrapper {
    padding: 1rem;
    width: 100%;
    display: flex;
    gap: 1.5rem;
    max-width: 1120px;


    .card {
        width: 20%;
        height: 15vh;
        margin: auto;
        border-radius: 10px;
        gap: 1.5rem;
        border: 1px solid gray;
        cursor: pointer;
        transition-duration: 0.5s;


        h4 {
            padding: 1.5rem;
            text-align: center;
            font-size: 20px;
            opacity: 0.8px;
        }

        h2 {
            text-align: center;
            font-size: 30px;
        }
    }

    .card:hover {
        color: blue;
        border: 1px solid blue;
    }

}

.main {
    padding: 1rem;
    width: 100%;
    display: flex;
    background: linear-gradient(0deg, #DFE0EB, #DFE0EB),
        l inear-gradient(0deg, #FFFFFF, #FFFFFF);
    border-radius: 8px;
    gap: 1.5rem;

    .text {
        font-size: 18px;
        line-height: 23px;
        letter-spacing: 0.4px;
        color: #252733;
        font-weight: bold;
        margin-left: 25px;

        h4 {
            font-size: 12px;
            line-height: 16px;
            letter-spacing: 0.1px;
            color: #9FA2B4;
        }
    }

    .text2 {
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 0.1px;
        color: #9FA2B4;
        margin-left: 600px;
        display: flex;
        margin-top: 20px;
        gap: 2.5rem;

    }

    .main__card {
        padding: 1rem;
        width: 200px;
        border: 1px solid gray;
        border: 1px solid #DFE0EB;
        flex-direction: column;

        h3 {
            font-size: 18px;
            line-height: 22px;
            letter-spacing: 0.3px;
            text-align: center;
            color: #9FA2B4;

        }

        h2 {
            font-size: 24px;
            line-height: 30px;
            letter-spacing: 0.3px;
            text-align: center;
            color: #252733;

        }
    }
}

