.form__wrapper{
    max-width: 500px;
    height: 50vh;
    margin: auto;
    padding: 0 4rem;
    margin-top: 160px;
    box-shadow: 0 0 8px;
    border-radius: 10px;
    margin-bottom: 10px;

    h1{
        font-size: 20px;
        line-height: 20px;
        letter-spacing: 0.2px;
        text-align: center;
    }


    form{
        display: flex;
        flex-direction: column;
      
        h1{
            padding: 5px;
            font-size: 14px;
            line-height: 16px;
            letter-spacing: 0.1px;
            color: black;
            margin-bottom: 5px;
        }

        input{
           width: 100%;
           height: 30px;
           border-radius: 5px;
           outline: none;
           margin-bottom: 10px;

           &::placeholder{
            font-size: 15px;
            color: rgb(202, 188, 188);
           }
        }
        input:focus{
            border: 1px solid blue;
        }
    }
    button{
        width: 100%;
        height: 40px;
        background-color: blue;
        border: none;
        border-radius: 10px;
        text-align: center;
        margin-bottom: 10px;
    }
    p{
        font-size: 20px;
        line-height: 16px;
        letter-spacing: 0.2px;
        display: flex;
        gap: 2rem;
    }
    span{
        list-style: none;

        a{
            text-decoration: none;
            color: blue;
        }
    }
}