form{
    width: 100%;
    padding: 1rem;
    margin: auto; 
    border-radius: 10px;
    display: flex;
    justify-content: space-evenly;

    input{
        width: 200px;
        height: 50px;
    }
    textarea{
        padding: 1.5rem;
        width: 200px;
        height: 50px;
    }
    button{
        width: 150px;
        height: 50px;
        background-color: black;
        color: white; 
        border-radius: 15px;
        letter-spacing: 0.3px;
        font-weight: bold;
        cursor: pointer;
        font-size: 20px;
    }
}
