.list-item{
    display: block;
    padding: 15px;
    display: flex;
    gap: 10px;
    text-decoration: none;
    color: #A4A6B3;
    font-size: 16px;
    line-height: 18px;
    letter-spacing: 0.2px;
    border-left: 2px solid #363740;

    svg{
        fill: #9FA2B4 ;
    }
    &.active{
        background-color: #9fa2b436 ;
        border-left: 2px solid white;
        color: #DDE2FF;

        svg{
            color: #DDE2FF;
        }
    }
}
aside .logo-dashboard{
    margin: 25px 15px 60px;
}